<template>
  <div class="follow-action-component">
    <!--  -->
    <div v-if="item.followAction == '1'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
          <span v-if="item.metadata.level" class="tag H">{{item.metadata.level}}</span>
      </p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>

    <!--  -->
    <div v-if="item.followAction == '2'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
          <span v-if="item.metadata.level" class="tag H">{{item.metadata.level}}</span>
      </p>
      <p>: {{item.remark | filterBlank}}</p>
