          <span v-if="item.metadata.level" class="tag H">{{item.metadata.level}}</span>
      </p>
      <p>: {{item.metadata.areaName | filterBlank}}</p>
      <p>: {{(item.metadata.invaiteDate || item.metadata.inviteDate) | filterBlank}}</p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>

    <!--  -->
    <div v-if="item.followAction == '5'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
          <span v-if="item.metadata.level" class="tag H">{{item.metadata.level}}</span>
      </p>
      <p v-if="item.metadata.defeat">: {{item.metadata.defeat.reasonType | filterBlank}}</p>
      <p v-if="item.metadata.defeat">: {{item.metadata.defeat.reasonDefeat | filterBlank}}</p>
      <p v-if="item.metadata.defeat">: {{item.metadata.defeat.remark | filterBlank}}</p>
    </div>

    <!-- () -->
    <div v-if="item.followAction == '6'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
          <span v-if="item.metadata.level" class="tag H">{{item.metadata.level}}</span>
      </p>
      <p>: {{item.metadata.areaName | filterBlank}}</p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>

    <!--  7 -->
    <div v-if="item.followAction == '7'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
      </p>
      <p v-if="item.metadata.level">: 
          <span class="tag" :class="item.metadata.level.oldLevel">{{item.metadata.level.oldLevel}}</span>
          <i class="el-icon-right"></i>
          <span class="tag" :class="item.metadata.level.newLevel">{{item.metadata.level.newLevel}}</span>
      </p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>

    <!--  -->
    <div v-if="item.followAction == '8'">
      <p v-if="item.metadata">
