      </p>
      <p>: {{item.metadata.areaName | filterBlank}}</p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>

    <!--  -->
    <div v-if="item.followAction == '10'">
      <p v-if="item.metadata">
