      <p>: {{item.metadata.areaName | filterBlank}}</p>
      <p>: {{item.metadata.advisorName | filterBlank}}({{item.metadata.advisorTel | filterBlank}})</p>
      <p v-if="item.metadata.visitDate">: {{item.metadata.visitDate || item.metadata.appointmentDate}}</p>
    </div>

    <!-- dcc || dcc -->
    <div v-if="item.followAction == '14' || item.followAction == '35'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
