          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
          <span v-if="item.metadata.level" class="tag" :class="item.metadata.level">{{item.metadata.level}}</span>
          <span v-if="item.metadata.grade" class="tag score">{{(item.metadata.grade !=0 && item.metadata.grade != 11) ? (item.metadata.grade + '') : '--'}}</span>
      </p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>

    <!--  -->
    <div v-if="item.followAction == '31'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
      </p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>

    <!--  -->
    <div v-if="item.followAction == '32'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
      </p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>

    <!--  -->
    <div v-if="item.followAction == '33'">
      <p v-if="item.metadata">
          : {{item.metadata.customer && item.metadata.customer.nickname | filterBlank}}
          <span v-if="item.metadata.level" class="tag" :class="item.metadata.level">{{item.metadata.level}}</span>
