          <span v-if="item.metadata.grade" class="tag score">{{(item.metadata.grade !=0 && item.metadata.grade != 11) ? (item.metadata.grade + '') : '--'}}</span>
      </p>
      <p>: {{item.remark | filterBlank}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
        item: {
            type: Object,
            default: {}
        }
    }
  }

</script>

<style lang="scss" scoped>
.follow-action-component{
  p {
    margin-bottom: 5px;
    color: #9ba6b6;
    font-size: 15px;
    .tag {
      margin-top: 0;
    }
    .tag:first-child {
      margin-left: 5px;
    }
  }

  @media screen and (max-width: 1485px){
    p {
      font-size: 14px;
    }
  }
}
</style>




