<template>
    
</template>

<script>
export default {
    data() {
        return {
            todoStat: {
                cluePendingTotal: 0, // 
                clueOvertimeTotal: 0, // 
                archivePendingTotal: 0, // 
                archiveOvertimeTotal: 0, // 
            },
            followStat: {
                clueHandleTotal: 0,  // 
                clueArchiveTotal: 0,  // 
                inviteTotal: 0,  // 
                inviteArrivalTotal: 0,  // 
                archiveDefeatedTotal: 0,  // 
                orderTotal: 0   //  DCC
            },
            callStat: {
                callIn: 0, // 
                callOut: 0,  // 
                talk: 0,  // 
                notTalk: 0,  // 
                talkTime: 0,  // 
            },
        }
    },
    methods: {
        // 
        queryStatTodo() {
            this.$fetch.work.getStatTodo().then((res) => {
                this.$handleResponse(res, data =>{
                    if (data) {
                        this.todoStat = data;
                    }
                }, false);
            }).catch(res => {
                console.log(res)
            })
        },
        // 
        queryStatFollow(params) {
            this.$fetch.work.getStatFollow(params).then((res) => {
                this.$handleResponse(res, data =>{
                    if (data) {
                        this.followStat = data;
                    }
                }, false);
            }).catch(res => {
                console.log(res)
            })
        },
        // 
        queryStatCall(params) {
            this.$fetch.work.getStatCall(params).then((res) => {
                this.$handleResponse(res, data =>{
                    if (data) {
                        this.callStat = data;
                    }
                }, false);
            }).catch(res => {
                console.log(res)
            })
        }
    },
}
</script>




